import Helper from '@/function.js';
export default {
	data() {
		return {
			imgSvr: "",
			token: "",
			info: {},
			teamCount: "",
			dataModel: {
				"taskMoney": 0.00,
				"taskProfit": 0.00,
				"taskPackageProfit": 0.00,
				"taskSuperCount": 0,
				"taskCount": 0,
				"useTaskSuperCount": 0,
				"useTaskCount": 0,
				"money": 0.00,
				"todayIncome": 0.0
			},
			rankName: "普通会员",
			levelName: "普通用户"
		}
	},
	onLoad: function() {
		this.imgSvr = this.ImgServer;
		this.token = uni.getStorageSync('token');
		//this.getTeamCount();
	},
	onShow: function() {
		this.getInfo();
		this.getData();

	},
	methods: {
		goCredit() {
			uni.navigateTo({
				url: "/pages/my/credit"
			})
		},
		getTeamCount() {
			var _this = this;

			uni.request({
				url: _this.ApiGateWay + '/sale/Member/GetTeamCount',
				data: "",
				method: 'GET',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					console.log(res);

					if (Helper.CheckData(res)) {
						_this.teamCount = (res.data.data.total - 1) + "人";
					} else {

					}
				},
				fail: function() {
					//uni.hideLoading();
				},
				complete: function() {
					//uni.hideLoading();
				}


			})
		},
		initRankName() {
			switch (this.info.rank) {
				case 0:
					this.rankName = "普通会员"
					break;
				case 1:
					this.rankName = "网红"
					break;
				case 2:
					this.rankName = "达人"
					break;
				case 3:
					this.rankName = "明星"
					break;
				case 4:
					this.rankName = "巨星"
					break;
			}

			switch (this.info.memberType) {
				case 0:
					this.levelName = "普通用户"
					break;
				case 1:
					this.levelName = "经销商"
					break;
				case 2:
					this.levelName = "代理商"
					break;
				case 3:
					this.levelName = "总代"
					break;
			}
		},
		getInfo() {
			var _this = this;
			uni.showLoading({
				title: "请稍候..."
			})
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/GetInfo',
				data: "",
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					console.log(res);

					if (Helper.CheckData(res)) {
						_this.info = res.data.data;
						_this.initRankName()
					} else {

					}
				},
				fail: function() {
					uni.hideLoading();
				},
				complete: function() {
					uni.hideLoading();
				}


			})
		},
		getData() {
			var _this = this;
			uni.showLoading({
				title: "请稍候..."
			})
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/GetTodayData',
				data: "",
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					console.log(res);

					if (Helper.CheckData(res)) {
						_this.dataModel = res.data.data;
					} else {

					}
				},
				fail: function() {
					uni.hideLoading();
				},
				complete: function() {
					uni.hideLoading();
				}


			})
		},
		shez() {
			uni.navigateTo({
				url: '/pages/my/set'
			})

		},
		team() {
			uni.navigateTo({
				url: '/pages/my/team'
			})

		},
		release() {
			uni.navigateTo({
				url: '/pages/my/release'
			})

		},
		person() {
			uni.navigateTo({
				url: '/pages/my/personal'
			})

		},
		taskmanmgent() {
			uni.navigateTo({
				url: '/pages/my/taskmanmgent'
			})

		},
		balance() {
			uni.navigateTo({
				url: '/pages/my/balance'
			})
		},
		invite() {
			uni.navigateTo({
				url: '/pages/invite/invite'
			})

		},
		myBank() {
			console.log()
			uni.navigateTo({
				url: '/pages/my/bank'
			})
		},
		withdrawals() {
			uni.navigateTo({
				url: '/pages/my/Withdrawals'
			})
		}





	}

}
